
import Vue from 'vue';

export default Vue.extend({
    name: 'MarkerBuilder',
    data() {
        return {
            form: {
                name: '',
                color: '#00BFFF',
                fontColor: '#000',
                size: '50',
            },
        };
    },
    methods: {
        createMarker(event: SubmitEvent | PointerEvent): void {
            event.preventDefault();
            this.$emit('createMarker', this.form);

            this.form.name = '';
        },
    },
});
